<template>
    <div class="content">
        <div class="talbar">
            <div class="wenzi" @click="routerToHome">返回</div>
            <div class="wenzi">说说</div>
            <div class="wenzi">搜索</div>
        </div>
        <div class="content">
            <div class="content_bar">
                <img src="../assets/image/male.jpg" alt="">
                <input placeholder="记录美好生活" type="text">
            </div>
            <div class="content_footer">
                <div class="wenzhang_item">
                    <div class="item">
                        <div class="person_information">
                            <img src="../assets/image/male.jpg" alt="">
                            <div>
                                <span>小政政</span>
                                <span>今天16:05</span>
                            </div>
                        </div>
                        <div class="person_wenzhang">
                            项目基本完成
                        </div>
                        <div class="person_caozuo">
                            <i class="iconfont icon-zan"></i>
                            <i class="iconfont icon-duanxin"></i>
                            <i class="iconfont icon-zhuanfa"></i>
                        </div>
                        <div class="person_pinglun">
                            <el-input type="text" show-word-limit placeholder="评论" v-model="input" maxlength="200" class="input"></el-input>
                        </div>
                    </div>
                </div>
         
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            input:""
        }
    },
    methods:{
        routerToHome(){
            this.$router.push("/home")
        }
    }
}
</script>
<style scoped>
.content{
    width: 100%;
    height: 100%;
    background-color: red;
}
.talbar{
    position: relative;
    width: 100%;
    height: 5rem;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 999;
    background-color: #fff;
    border-bottom: 1px solid #000;
}
.wenzi{
    font-size: 18px;
    font-weight: 700;
    color: #000;
}
.wenzi:nth-child(1),
.wenzi:nth-child(3){
    margin: 0 10px;
}
.content{
    height: 100%;
    width: 100%;
    background-color: #fff;
}
.content_bar{
    width: 100%;
    position: relative;
    height: 5rem;
    padding-top: 5rem;
    display: flex;
    border-bottom: 15px solid rgb(213, 204, 204) ;
}
.content_bar>img{
    width: 3rem;
    height: 3rem;
    margin: 10px;
    border-radius: 50%;
}
.content_bar>input{
    margin-top: 10px;
    margin-left: 10px;
    width: calc(73%);
    height: 3rem;
    border: none;
    border-radius: 20px;
    background-color:rgb(246, 238, 238);
    padding-left: 20px;
    text-decoration: none;
    outline: none;
}
.content_footer{
      width: 100%;
      height: 100%;
      background-color: #fff;
  }
  .choose_item{
      position: relative;
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      width: 100%;
      height: 5rem;
      border-bottom: 8px solid rgb(213, 204, 204);
  }
  .choose_item>.item{
      position: relative;
      display: flex;
      flex-direction: column;
      text-align: center;
  }
  .choose_item>.item>i{
      font-size: 33px;
      font-weight: 400;
      color: #000;
  }
  .choose_item>.item:nth-child(2)>i{
      font-weight: 800;
  }
  .choose_item>.item>span{
      color: #000;
      font-weight: 500;
      text-align: center;
  }
  .choose_item>.item::before{
      content: "";
      position: absolute;
      top: -5px;
      right: -10px;
      width: 10px;
      height: 10px;
      background-color: red;
      border-radius: 50%;
  }
  .wenzhang_item{
      width: 100%;
      height: 100%;
  }
  .wenzhang_item>.item{
      min-height: 15rem;
      /* background-color: red; */
      display: flex;
      flex-direction: column;
      position: relative;
      padding-left: 10px;
      border-bottom: 15px solid rgb(213, 204, 204);
  }
  .wenzhang_item>.item>.person_information{
      min-height: 5rem;
      width: 100%;
      /* background-color: pink; */
      margin-top:2rem;
      display: flex;
      align-items: center;
  }
  .person_information>img{
      width: 3rem;
      height: 3rem;
      border-radius: 50%;
  }
  .person_information>div{
      padding-left: 10px;
      display: flex;
      flex-direction: column;
  }
  .person_wenzhang{
      width: 100%;
      min-height: 4rem;
      /* background-color: skyblue; */
  }
  .person_caozuo{
      width: calc(100%);
      text-align: right;
  }
  .person_caozuo>i{
      font-size: 20px;
      margin-right: 10px;
      font-weight: 600;
  }
  .person_pinglun{
      width: calc(100% - 10px);
      height: 4rem;
      /* background-color: orangered; */
  }
  .person_pinglun>.input{
      line-height: 4rem;
}
</style>


